"use client"

import type React from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { ChevronLeft, Upload, X, CheckCircle, Loader2 } from "lucide-react"
import Link from "next/link"
import { useState, useRef, use, useEffect } from "react"
import { useRouter } from "next/navigation"
import Image from "next/image"

// Mapeo de categorías (IDs numéricos para la BD)
const categorias = [
  { id: "1", slug: "electronica", nombre: "Electrónica" },
  { id: "2", slug: "muebles", nombre: "Muebles" },
  { id: "3", slug: "ropa", nombre: "Ropa y Accesorios" },
  { id: "4", slug: "hogar", nombre: "Hogar y Cocina" },
  { id: "5", slug: "deportes", nombre: "Deportes" },
  { id: "6", slug: "libros", nombre: "Libros y Educación" },
  { id: "7", slug: "juguetes", nombre: "Juguetes y Juegos" },
  { id: "8", slug: "herramientas", nombre: "Herramientas" },
  { id: "9", slug: "vehiculos", nombre: "Vehículos" },
  { id: "10", slug: "electrodomesticos", nombre: "Electrodomésticos" },
  { id: "11", slug: "otros", nombre: "Otros" },
]

const ciudadesValle = ["Cali", "Palmira", "Buenaventura", "Tuluá", "Cartago", "Buga", "Jamundí", "Yumbo"]

export default function PublicarCategoriaPage({ params }: { params: Promise<{ categoria: string }> }) {
  const resolvedParams = use(params)
  const router = useRouter()
  const fileInputRef = useRef<HTMLInputElement>(null)

  // Estados
  const [titulo, setTitulo] = useState("")
  const [descripcion, setDescripcion] = useState("")
  
  // Inicializar categoría basada en la URL
  const catInicial = categorias.find(c => c.slug === resolvedParams.categoria)?.id || "1"
  const [categoriaId, setCategoriaId] = useState(catInicial)
  
  const [condicion, setCondicion] = useState("Usado")
  const [tipo, setTipo] = useState<"venta" | "donacion" | "intercambio">("venta")
  const [precio, setPrecio] = useState("")
  const [ciudad, setCiudad] = useState("Cali")
  const [imagenes, setImagenes] = useState<string[]>([])

  const [isLoading, setIsLoading] = useState(false)
  const [showSuccess, setShowSuccess] = useState(false)
  const [error, setError] = useState("")
  const [userId, setUserId] = useState<number | null>(null)

  useEffect(() => {
    const storedUserId = localStorage.getItem("zirako_user_id")
    if (storedUserId) setUserId(Number.parseInt(storedUserId))
  }, [])

  // --- FUNCIÓN PARA LIMPIAR EL FORMULARIO ---
  const limpiarFormulario = () => {
      setTitulo("")
      setDescripcion("")
      setPrecio("")
      setImagenes([])
      setCondicion("Usado")
      // Mantenemos la categoría y ciudad actual por comodidad
      setShowSuccess(false)
  }

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files
    if (!files) return
    const maxImages = 5 - imagenes.length
    Array.from(files).slice(0, maxImages).forEach((file) => {
      if (file.type.startsWith("image/")) {
        const reader = new FileReader()
        reader.onload = (event) => {
          if (event.target?.result) setImagenes((prev) => [...prev, event.target!.result as string])
        }
        reader.readAsDataURL(file)
      }
    })
    e.target.value = "" // Reset input
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setIsLoading(true)

    try {
      if (!titulo.trim()) throw new Error("El título es requerido")
      if (!descripcion.trim()) throw new Error("La descripción es requerida")
      if (tipo === "venta" && !precio) throw new Error("El precio es requerido")
      if (imagenes.length === 0) throw new Error("Debes agregar al menos una imagen")

      const response = await fetch("/api/items", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({
          userId,
          titulo: titulo.trim(),
          descripcion: descripcion.trim(),
          categoria_id: Number(categoriaId),
          tipo,
          condicion,
          precio: tipo === "venta" ? Number.parseFloat(precio.replace(/[^0-9]/g, "")) : 0,
          ciudad,
          imagenes,
        }),
      })

      const data = await response.json()
      if (!response.ok) throw new Error(data.error || "Error al publicar")

      setShowSuccess(true)
    } catch (err: any) {
      setError(err.message)
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-primary">
      <div className="container mx-auto px-4 py-8 max-w-md">
        <div className="flex items-center gap-4 mb-8">
          <Link href="/publicar">
            <Button variant="outline" className="bg-accent text-accent-foreground border-accent hover:bg-accent/90 h-10 px-4 flex items-center gap-2"><ChevronLeft className="h-5 w-5" /> Regresar</Button>
          </Link>
          <h1 className="text-2xl font-bold text-primary-foreground">Publicar artículo</h1>
        </div>

        {error && <div className="bg-red-500/20 border border-red-500 text-red-200 px-4 py-3 rounded-lg mb-6">{error}</div>}

        <Card className="bg-secondary border-secondary p-6">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* TÍTULO */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Título *</Label>
              <Input value={titulo} onChange={(e) => setTitulo(e.target.value)} className="bg-white border-gray-300 text-gray-900" required />
            </div>

            {/* CATEGORÍA */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Categoría *</Label>
              <Select value={categoriaId} onValueChange={setCategoriaId}>
                <SelectTrigger className="bg-white border-gray-300 text-gray-900"><SelectValue /></SelectTrigger>
                <SelectContent>
                  {categorias.map((cat) => <SelectItem key={cat.id} value={cat.id}>{cat.nombre}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>

            {/* TIPO (VENTA/DONACIÓN/INTERCAMBIO) */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Tipo *</Label>
              <div className="grid grid-cols-3 gap-2">
                <Button type="button" variant="outline" onClick={() => setTipo("venta")} className={tipo === "venta" ? "bg-accent text-accent-foreground" : "bg-white text-gray-900"}>Venta</Button>
                <Button type="button" variant="outline" onClick={() => setTipo("donacion")} className={tipo === "donacion" ? "bg-accent text-accent-foreground" : "bg-white text-gray-900"}>Donación</Button>
                <Button type="button" variant="outline" onClick={() => setTipo("intercambio")} className={tipo === "intercambio" ? "bg-accent text-accent-foreground" : "bg-white text-gray-900"}>Intercambio</Button>
              </div>
            </div>

            {/* CONDICIÓN */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Condición *</Label>
              <Select value={condicion} onValueChange={setCondicion}>
                <SelectTrigger className="bg-white border-gray-300 text-gray-900"><SelectValue /></SelectTrigger>
                <SelectContent>
                  <SelectItem value="Nuevo">Nuevo</SelectItem>
                  <SelectItem value="Como nuevo">Como nuevo</SelectItem>
                  <SelectItem value="Usado">Usado</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* PRECIO (Solo si es venta) */}
            {tipo === "venta" && (
              <div className="space-y-2">
                <Label className="text-primary-foreground font-semibold">Precio *</Label>
                <Input type="number" value={precio} onChange={(e) => setPrecio(e.target.value)} className="bg-white border-gray-300 text-gray-900" />
              </div>
            )}

            {/* CIUDAD */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Ciudad *</Label>
              <Select value={ciudad} onValueChange={setCiudad}>
                <SelectTrigger className="bg-white border-gray-300 text-gray-900"><SelectValue /></SelectTrigger>
                <SelectContent>
                  {ciudadesValle.map((c) => <SelectItem key={c} value={c}>{c}</SelectItem>)}
                </SelectContent>
              </Select>
            </div>

            {/* DESCRIPCIÓN */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Descripción *</Label>
              <Textarea value={descripcion} onChange={(e) => setDescripcion(e.target.value)} className="bg-white border-gray-300 text-gray-900 min-h-32" required />
            </div>

            {/* IMÁGENES */}
            <div className="space-y-2">
              <Label className="text-primary-foreground font-semibold">Imágenes ({imagenes.length}/5)</Label>
              <div className="grid grid-cols-3 gap-2">
                  {imagenes.map((img, i) => (
                      <div key={i} className="relative aspect-square bg-gray-200 rounded overflow-hidden">
                          <Image src={img} alt="preview" fill className="object-cover"/>
                          <button type="button" onClick={() => setImagenes(p => p.filter((_, idx) => idx !== i))} className="absolute top-1 right-1 bg-red-500 text-white rounded-full p-1"><X className="h-3 w-3"/></button>
                      </div>
                  ))}
                  {imagenes.length < 5 && (
                      <div onClick={() => fileInputRef.current?.click()} className="aspect-square border-2 border-dashed border-accent rounded flex items-center justify-center cursor-pointer bg-primary/50">
                          <Upload className="h-6 w-6 text-accent"/>
                      </div>
                  )}
              </div>
              <input ref={fileInputRef} type="file" accept="image/*" multiple className="hidden" onChange={handleImageChange} />
            </div>

            <Button type="submit" disabled={isLoading} className="w-full bg-accent text-accent-foreground hover:bg-accent/90 h-12 text-lg font-bold">
              {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : "Publicar"}
            </Button>
          </form>
        </Card>
      </div>

      <Dialog open={showSuccess} onOpenChange={setShowSuccess}>
        <DialogContent className="bg-secondary border-secondary text-primary-foreground">
            <div className="flex justify-center mb-4"><CheckCircle className="h-16 w-16 text-green-500" /></div>
            <DialogTitle className="text-center text-2xl">¡Publicado!</DialogTitle>
            <DialogDescription className="text-center text-primary-foreground/70">Tu artículo ya es visible.</DialogDescription>
            <div className="flex flex-col gap-3 mt-4">
                <Button onClick={() => router.push("/perfil/mis-articulos")} className="bg-accent text-accent-foreground">Ver mis artículos</Button>
                
                {/* BOTÓN CORREGIDO: Llama a limpiarFormulario */}
                <Button onClick={limpiarFormulario} variant="outline" className="border-primary text-primary bg-white font-bold">Publicar otro artículo</Button>
            </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}